.Profile {
  display: flex;
}
.wrapper_profile_left {
  max-width: 795px;
  margin-right: auto;
}

.profile_photo {
  padding-top: 13px;
  margin-top: 22px;
  vertical-align: top;
  border-radius: 4px;
  content: "";
  max-width: 230px;
  background-color: #333336 !important;
  color: white;
  min-height: 270px;
}
.profile_photo img {
  margin: 10px 14px 14px 14px;

  margin-bottom: 10px;
  margin-top: 0px;
  cursor: pointer;
}
.flex_profile_photo {
  max-width: 230px;
  display: flex;
  justify-content: center;
  margin-top: 17px;
  align-items: center;
}
.bt1_photo {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-right: 10px;
  font-size: 12.5px;
  background-color: #444446;
  border: none;
  outline: none;
  padding: 7px 36px 8px 36px;
  margin-top: -20px;
  border-radius: 4px;
  color: #ccc;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Helvetica Neue",
    Geneva, "Noto Sans Armenian", "Noto Sans Bengali", "Noto Sans Cherokee",
    "Noto Sans Devanagari", "Noto Sans Ethiopic", "Noto Sans Georgian",
    "Noto Sans Hebrew", "Noto Sans Kannada", "Noto Sans Khmer", "Noto Sans Lao",
    "Noto Sans Osmanya", "Noto Sans Tamil", "Noto Sans Telugu", "Noto Sans Thai",
    sans-serif, arial, Tahoma, verdana;
}
.bt1_photo:hover {
  color: rgba(204, 204, 204, 0.746);
  background-color: #444446c7;
}
.bt2_photo {
  margin-top: -20px;
  text-align: center;
  padding: 7px 13px 8px 13px;
  color: #ccc;
  cursor: pointer;
  text-align: center;
  background: 0 0;
  outline: none;
  background-color: #444446;
  border: 0;
  border-radius: 4px;
}
.bt2_photo:hover {
  color: rgba(204, 204, 204, 0.746);
  background-color: #444446c7;
}

.profile_gifts {
  margin: 15px 0;
  max-width: 230px;
  min-height: 115px;
  background-color: #333336 !important;
  color: white;
  border-radius: 4px;
}
.top_profile_gifts {
  display: flex;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Helvetica Neue",
    Geneva, "Noto Sans Armenian", "Noto Sans Bengali", "Noto Sans Cherokee",
    "Noto Sans Devanagari", "Noto Sans Ethiopic", "Noto Sans Georgian",
    "Noto Sans Hebrew", "Noto Sans Kannada", "Noto Sans Khmer", "Noto Sans Lao",
    "Noto Sans Osmanya", "Noto Sans Tamil", "Noto Sans Telugu", "Noto Sans Thai",
    sans-serif, arial, Tahoma, verdana;
  width: 100%;
  min-height: 25px;
  padding-top: 14px;
  padding-left: 14px;
  cursor: pointer;
}
.top_profile_gifts h1 {
  font-size: 12.5px;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  font-weight: 400;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: auto;
  font-size: 12.5px;
  margin-right: 5px;
  letter-spacing: 0;
}
.top_profile_gifts span {
  font-size: 12px;
  font-weight: 400;
  color: rgb(147, 147, 147);
}
.flex_gifts_bottom {
  margin-top: 8px;
  display: flex;
}
.flex_gifts_bottom img {
  cursor: pointer;
  width: 64px;
  margin-left: 9px;
  height: 64px;
}

.wrapper_profile_right {
  width: 550px;
}
.profile_about {
  background-color: #333336 !important;
  border-radius: 4px;
  margin: 22px 0px 0 15px;
  min-height: 310px;
}
.wrapper_profile_about_top {
  max-width: 495px;
  margin: 0 auto;
  min-height: 60px;
}
.profile_about_name {
  display: flex;
}
.profile_about_name h1 {
  color: rgb(255, 255, 255);
  direction: ltr;
  display: block;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans",
    "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali",
    "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic",
    "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada",
    "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil",
    "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
  font-size: 19px;
  font-weight: 400;
  line-height: 25px;
  overflow-x: hidden;
  overflow-y: hidden;
  padding-left: 1px;
  text-align: left;
  text-overflow: ellipsis;
  margin-top: 10px;
  -webkit-font-smoothing: subpixel-antialiased;
}

.profile_about_name h1 img {
